.rotateAni {
	animation-name: cssAnimation;
	animation-duration: 3s;
	animation-iteration-count: 1;
  	animation-timing-function: ease;
 	animation-fill-mode: forwards;
}

@keyframes cssAnimation {
	from {
	  -webkit-transform: rotate(0deg) scale(0.5) skew(0deg) translate(100px);
	}
	to {
	  -webkit-transform: rotate(0deg) scale(1) skew(0deg) translate(100px);
	}
  }
.container-main {
	width: 100%;
}
.tap-target {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 12em;
	height: 12em;
	padding: 1em;
	margin: 0 auto;
	box-sizing: border-box;
  
	border-radius: 100%;
  
	font-size: 1.125em;
	text-align: center;
	color: #fff;
	background-color: indianred;
  
	cursor: pointer;
	touch-action: none;
	user-select: none;
  
	transition: all 0.3s;
  }

.tap-hexagon {
	font-size: 1.125em;
	text-align: center;
	align-items: center;
	color: #fff;
	background-color: indianred;
  
	cursor: pointer;
	touch-action: none;
	user-select: none;
  
	transition: all 0.5s;
  
}

.tap-hexagon.switch-bg {
	background-color: #f40;	
}

.tap-hexagon.large {
	-webkit-transform: scale(1.25);
	transform: scale(1.25);
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
  }
  
  .tap-target.switch-bg {
	background-color: #f40;
  }
  
  .tap-target.large {
	-webkit-transform: scale(1.25);
	transform: scale(1.25);
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
  }
  
  .rotate {
	-webkit-transform: rotate(360deg);
	transform: rotate(360deg);
  }


