#hexagon {
	width: 100px;
	height: 57.735px;
	background:#05c2c9;
	position: relative;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  #hexagon::before {
	content: "";
	position: absolute;
	top: -28.8675px;
	left: 0;
	width: 0;
	height: 0;
	border-left: 50px solid transparent;
	border-right: 50px solid transparent;
	border-bottom: 28.8675px solid #05c2c9;
  }
  #hexagon::after {
	content: "";
	position: absolute;
	bottom: -28.8675px;
	left: 0;
	width: 0;
	height: 0;
	border-left: 50px solid transparent;
	border-right: 50px solid transparent;
	border-top: 28.8675px solid #05c2c9;
  }


  /***** Zoom *****/
  .zoom a:hover{
	transform: scale(1.1);
	transition: all ease 500ms;
  }

  .progressbar-container {
	position: relative;
	width: 100%;
	height: 50px;
	border: 1px solid #fff;
	border-radius: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}
.progressbar-container .progressbar-complete {
	position: absolute;
	left: 0;
	top: 0px;
	height: 100%;
	background-color: #5225bd;
	border-radius: 10px;
	animation: g 2500ms infinite ease-in-out;
	z-index: 2;
}

.progressbar-container .progressbar-complete .progressbar-liquid {
	overflow: hidden;
	z-index: 1;
	width: 70px;
	height: 70px;
	animation: g 2500ms infinite ease-in-out, r 3000ms infinite cubic-bezier(0.5, 0.5, 0.5, 0.5);
	position: absolute;
	right: -5px;
	top: -10px;
	background-color: #5225bd;
	border-radius: 40%;
}
.progressbar-container .progress {
	z-index: 2;
}

@keyframes g {
	0% { background-color: salmon; }
	100% { background-color: salmon; }
  }
  
  @keyframes r {
	from { transform: rotate(0deg); }
	from { transform: rotate(360deg); }
  }
